/* component */

/* screebs */

* {
  box-sizing: border-box;
}

input {
  all: unset;
  box-sizing: border-box;
  -webkit-appearance: none;
          appearance: none;
}

body {
  background-color: #dee3eb;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: black;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  margin-bottom: 10px;
  background-color: white;
  color: black;
}

.formBtn {
  margin-top: 10px;
  cursor: pointer;
  width: 150px;
  padding: 7px 0px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;
  background-color: rgb(243, 102, 77);
  margin-top: 0px;
}

.displayUserName {
  font-weight: bold;
  color: #385498;
}

.footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-bottom: 10px;
}

.noUserName {
  font-size: 15px;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.noUserName__icon {
  margin-bottom: 10px;
  color: #385498;
  -webkit-animation: up-down 1.3s infinite;
          animation: up-down 1.3s infinite;
}
@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  30% {
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px);
  }
  60% {
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px);
  }
  100% {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}
@keyframes up-down {
  0% {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
  30% {
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px);
  }
  60% {
    -webkit-transform: translatey(-20px);
            transform: translatey(-20px);
  }
  100% {
    -webkit-transform: translatey(0px);
            transform: translatey(0px);
  }
}

